import React from 'react';
import { Row, Col } from 'antd';
import Filter from './Filter';
import YuJingChuLi from './YuJingChuLi';
import YuJingLeiBie from './YuJingLeiBie';
import XueYuanYuJingChuLi from './XueYuanYuJingChuLi';
import BuTongXueYuanYuJing from './BuTongXueYuanYuJing';
import YuJingQingKuang from './YuJingQingKuang';

export default class ZongTiGaiKuang extends React.Component {
    state = {
        filterValue: {},
    }
    
    getFilterValue = (filterValue) => {
		this.setState({filterValue})
    }

    render() {
        return (
            <React.Fragment>
                <Filter getFilterValue={this.getFilterValue}/>
                <Row gutter={20} style={{padding: 20}}>
                    {/* 预警处理结果 */}
                    <Col span={12} style={{marginBottom: 20}}>
                        <YuJingChuLi height={300}/>
                    </Col>

                    {/* 预警类别分布 */}
                    <Col span={12} style={{marginBottom: 20}}>
                        <YuJingLeiBie height={300}/>
                    </Col>

                    {/* 学院预警处理情况 */}
                    <Col span={24} style={{marginBottom: 20}}>
                        <XueYuanYuJingChuLi height={300}/>
                    </Col>

                    {/* 不同学院预警分析（类型） */}
                    <Col span={24} style={{marginBottom: 20}}>
                        <BuTongXueYuanYuJing height={300}/>
                    </Col>

                    {/* 预警情况分析 */}
                    <Col span={24}>
                        <YuJingQingKuang height={300}/>
                    </Col>
                </Row>
            </React.Fragment>
        )
    }
}
